h1 {
    font-family: Roboto;
    font-size: calc(18px + (36 - 18) * ((100vw - 300px) / (1600 - 300)));
    font-weight: Bold;
}

.source {
    margin-top: 10px;
    color: #dbcfe5;
}

.body {
    margin-top: 15px;
    margin-bottom: 15 px;
}

#body p {
    margin-top: 30px;
    font-size: calc(16px + (26 - 16) * ((100vw - 300px) / (1600 - 300)));
    font-family: Roboto;
    font-weight: Normal;
    text-align: justify;
}

.article-title {
    color: #db947d;
    font-family: "Verdana", sans-serif;
    border-bottom: 1px solid #503a40;
    font-size: calc(16px + (32 - 16) * ((100vw - 300px) / (1600 - 300)));

}

.article {
    padding: 40px;
    margin-top: 30px;
    margin-bottom: 30px;
}

.shadowed {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.creation-date {
    float: right;
    font-size: calc(16px + (20 - 16) * ((100vw - 300px) / (1600 - 300)));
    font-family: Roboto;
}

.abstract {
    margin-top: 20px;
    font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1600 - 300)));
    font-family: Roboto;
    font-weight: Normal;
    color: #dbcfe5;
    text-align: justify;
    line-height: 1.1em;
}

.content {
    color: #D4E2E4;
}

.center {
    width: 60%;
    height: 200px;
    margin: 0 auto;
    background-color: #ffa500;
}

.full-screen {
    width: 100%;
    height: 100%;
    margin: 0px;
}

.topnav {
    background-color: black;
    border-bottom: 10px solid #0000ff;
}

/* Not found page */
.error-template {
    padding: 40px 15px;
    text-align: center;
}
.error-actions {
    margin-top: 15px;
    margin-bottom: 15px;
}
.error-actions .btn {
    margin-right: 10px;
}
